import React, { createContext, useReducer, useContext, useCallback } from 'react';

type ContextType = {
	searchQuery: string;
	setSearchQuery: (query: string) => void;
};

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const StaffManagementContext = createContext<ContextType>(null!);
type SetSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string;
};

type StateType = {
	searchQuery: string;
};

type Actions = SetSearchQuery;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		default:
			return state;
	}
};

export function StaffManagementContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, { searchQuery: '' });

	const setSearchQuery = useCallback((query: string) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = {
		searchQuery: state.searchQuery,
		setSearchQuery
	} as ContextType;

	return (
		<StaffManagementContext.Provider value={contextValues}>
			{children}
		</StaffManagementContext.Provider>
	);
}

export const useStaffManagement = () => {
	const staffContext = useContext(StaffManagementContext);

	if (!staffContext)
		throw new Error('useBusinessContext must be used within the BusinessContextProvider');
	return staffContext;
};
