import React, { useCallback } from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import StaffFormElement from './StaffFormElement';

type Props = {
	modalHandler: any;
	hasToShowGuard: () => void;
	formValuesSetter: (fieldValues: string[]) => void;
	fetchStaffCount: () => void;
};

export default function CreateStaffForm({
	modalHandler,
	hasToShowGuard,
	formValuesSetter,
	fetchStaffCount
}: Props) {
	const { companyId } = useAppState();
	const closeModal = useCallback(() => modalHandler(null), []);
	const { showNotification } = useNotistack();
	return (
		<div>
			{companyId ? (
				<EntityForm.Main
					entity="staffEmployee"
					name={`staffEmployee-${companyId}`}
					url={`${companyId}/employee/`}
					method="post"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					prependData
					fields={[
						{
							name: 'avatar',
							required: false,
							value: null,
							onSubmitKey: '',
							type: 'object',
							onSubmitValue: (value) => (value ? value : null)
						},
						{
							name: 'first_name',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'username',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'last_name',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'mobile',
							//required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'roles',
							required: true,
							value: { label: 'Member', value: 'member' },
							type: 'object',
							onSubmitKey: '',
							onSubmitValue: (value) => [value.value]
						},
						{
							name: 'departments',
							onSubmitValue: (value) =>
								value.reduce((acc: any, curr: any) => [...acc, { id: curr.value }], []),
							value: [],
							required: false,
							type: 'array',
							onSubmitKey: ''
						},
						{
							name: 'can_send_news',
							required: false,
							value: true,
							onSubmitKey: ''
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: 'Success! ' });
						fetchStaffCount();
						resetForm();
						closeModal();
					}}
					onError={(error, resetForm): any => {
						showNotification({
							message: error?.response?.data?.message ?? 'Something went wrong',
							variant: 'error'
						});

						resetForm();
					}}
				>
					{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('values create staff', { values, errors });
						}

						return (
							<StaffFormElement
								values={values}
								setFieldValue={setFieldValue}
								setFieldError={setFieldError}
								isSubmitting={isSubmitting}
								errors={errors}
								formValuesSetter={formValuesSetter}
								closeModal={hasToShowGuard}
								resetForm={resetForm}
							/>
						);
					}}
				</EntityForm.Main>
			) : null}
		</div>
	);
}

CreateStaffForm.defaultProps = {
	onSuccess: () => {}
};
