import React from 'react';
import {
	ActionsButtons,
	ConfirmModal,
	ErrorBoundary,
	KntButton,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import { TableBody, TableContainer, TableHead, WhiteModal } from 'shared/components/Elements';
import { usePagination, useRouter } from '../../../shared/hooks';
import { useStaffManagement } from '../context/StaffManagementContext';
import { BsFillTelephoneFill } from 'react-icons/bs';
import Pagination from '@material-ui/lab/Pagination';
import { useDebounce } from 'use-debounce';
import EditStaffForm from './EditStaffForm';
import { useStaffManagementList } from '../hooks/useStaffManagement';
import { getBusinessIcon } from '../../../shared/services/utils/getBusinessIcon';
import { StaffManagementItemRequest } from '../../../shared/typings/staffManagement.types';
import { Link } from 'react-router-dom';
import clsx from 'clsx';
import { formRequest } from '../../../modules/entity/actions';
import { useEntityActionPopup, useLoadAll, useNotistack } from 'shared/hooks';
import { useDispatch } from 'react-redux';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { StatusTag } from 'shared/components/DataDisplay';

const NO_MOBILE_NUMBER = 'No mobile number';

type Props = {
	fetchStaffCount?: () => void;
};

function StaffManagementTableRequest({ fetchStaffCount = () => {} }: Props) {
	const { companyId, isAdmin } = useAppState();
	const { searchQuery } = useStaffManagement();
	const { company, userData } = useAuth();
	const { page, handlePage } = usePagination();
	const { match, history } = useRouter();
	const { showNotification } = useNotistack();
	const dispatch = useDispatch();
	const { reloadEntityAll } = useLoadAll();

	const isBusinessOwner = userData?.id === company?.company?.owner?.id;

	const {
		deleteLoading,
		handleClientDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible,
		editVisible,
		showEditVisible,
		closeEditPopup,
		staffCountLoading
	} = useStaffManagementList(fetchStaffCount);

	const [querySearch] = useDebounce<string>(searchQuery, 500);

	const {
		visible: resendVisible,
		showVisible: showResendVisible,
		loading: resendLoading,
		toggleLoading: toggleResendLoading,
		closePopup: closeResendPopup
	} = useEntityActionPopup<any>();

	const deleteClientHandler = () => () => handleClientDelete(deleteVisible!);

	const handleResendRequest = (id: number) => {
		toggleResendLoading();
		dispatch(
			formRequest({
				entity: 'staffEmployee',
				name: `staffEmployee-${companyId}`,
				method: 'post',
				url: `/${companyId}/employee/${id}/resend_invitation/`,
				values: {},
				cb: {
					success: () => {
						reloadEntityAll({
							entity: 'staffEmployee',
							name: `staffEmployee-${companyId}`,
							url: `/${companyId}/employee/`,
							params: {
								page: 1,
								limit: 10
							}
						});
						closeResendPopup();
						showNotification({ message: 'Successfully resended', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleResendLoading();
					}
				},
				params: {},
				primaryKey: 'id'
			})
		);
	};

	const handleResend = () => () => handleResendRequest(resendVisible!);

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity={'staffEmployee'}
					name={`staffEmployee-${companyId}`}
					url={`/${companyId}/employee/`}
					replaceIds
					params={{
						limit: 10,
						extra: {
							search: querySearch
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const requests = items.map((item: unknown) => item) as StaffManagementItemRequest[];

						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '22%' }}>
													Full name
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '24%' }}>Email</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '18%' }}>
													Phone number
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '12%' }}>Role</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '10%' }}>Status</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '14%', textAlign: 'center' }}>
													Actions
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{requests.map((item: StaffManagementItemRequest) => {
												const {
													id,
													first_name,
													last_name,
													username,
													mobile,
													roles,
													state,
													avatar,
													is_business_owner
												} = item;
												const phone = mobile
													? mobile[0] === '+'
														? mobile
														: `+${mobile}`
													: NO_MOBILE_NUMBER;

												return (
													<StyledTableRow key={id}>
														<StyledTableCell>
															<div className="flex items-center">
																<div className="h-9 w-9 flex-shrink-0">
																	{getBusinessIcon(
																		avatar?.thumbnail || avatar?.thumbnail_150,
																		first_name
																	)}
																</div>
																{state === 'Pending' ? (
																	<div className="truncate">
																		<span className="ml-2 text-sm font-medium">
																			{first_name} {last_name}
																		</span>
																	</div>
																) : (
																	<Link className="truncate" to={`${match.path}/view/${item.id}`}>
																		<span className="ml-2 text-sm font-medium text-primary transition hover:text-primary-dark">
																			{first_name} {last_name}
																		</span>
																	</Link>
																)}
															</div>
														</StyledTableCell>
														<StyledTableCell
															style={{
																overflow: 'hidden',
																textOverflow: 'ellipsis',
																whiteSpace: 'nowrap'
															}}
														>
															<span className="test-kdark-text text-sm">{username}</span>
														</StyledTableCell>
														<StyledTableCell>
															{phone ? (
																<div className="flex items-center">
																	<BsFillTelephoneFill color="#979797" size="15px" />
																	<a
																		href={`tel:${phone}`}
																		className={clsx('test-kdark-text ml-2 align-middle text-sm ', {
																			'text-primary hover:text-primary-dark':
																				phone !== NO_MOBILE_NUMBER,
																			'pointer-events-none': phone === NO_MOBILE_NUMBER
																		})}
																	>
																		{phone}
																	</a>
																</div>
															) : null}
														</StyledTableCell>
														<StyledTableCell>
															{roles.map((role: string, index: number) => {
																return (
																	<span className="text-sx text-kgrey" key={`${index}_${role}`}>
																		{' ' + role + ' '}
																		{roles.length > 1 ? (
																			<span className="text-kgrey">|</span>
																		) : null}
																		{is_business_owner ? <span>| owner</span> : null}
																	</span>
																);
															})}
															{state === 'Pending' ? (
																<KntButton color="primary" onClick={() => showResendVisible(id)}>
																	Resend
																</KntButton>
															) : null}
														</StyledTableCell>
														<StyledTableCell>
															<StatusTag status={state}>{state}</StatusTag>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center">
																{!is_business_owner && (
																	<ActionsButtons.DeleteButton
																		isDisabled={
																			isBusinessOwner ? false : is_business_owner || !isAdmin
																		}
																		clickHandler={() => showDeleteVisible(item)}
																	/>
																)}

																<ActionsButtons.ViewButton
																	isDisabled={!isAdmin || state === 'Pending'}
																	clickHandler={() => history.push(`${match.path}/view/${id}`)}
																/>
																<ActionsButtons.EditButton
																	isDisabled={
																		isBusinessOwner
																			? state === 'Pending'
																			: is_business_owner || !isAdmin || state === 'Pending'
																	}
																	clickHandler={() => showEditVisible(item)}
																/>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{isFetched && staffCountLoading && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteClientHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={`Are you sure you want to delete the following staff member: "${deleteVisible?.first_name} ${deleteVisible?.last_name}"`}
									hasLoader
									isLoading={deleteLoading}
								/>
								<ConfirmModal
									featureHandler={handleResend()}
									isOpen={Boolean(resendVisible)}
									toggleHandler={closeResendPopup}
									title="Are you sure to resend the Request"
									hasLoader
									isLoading={resendLoading}
								/>
								<WhiteModal
									handleModalOpen={closeEditPopup}
									open={Boolean(editVisible)}
									maxWidth={490}
									minWidth={490}
									scrollable
									noPadding
								>
									<EditStaffForm entity={editVisible!} onSuccess={closeEditPopup} />
								</WhiteModal>
								<div className="news__pagination my-4 flex items-center justify-between">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
									<div className="ml-4">
										<div className="text-base font-medium dark:text-white">
											Staff Count:{' '}
											<span className="ml-2 text-base font-semibold text-primary ">
												{meta?.count}
											</span>
										</div>
									</div>
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export function StaffManagementTable(props: Props) {
	return (
		<ErrorBoundary>
			<StaffManagementTableRequest {...props} />
		</ErrorBoundary>
	);
}
