import React, { useCallback, useRef, useState } from 'react';
import { ConfirmModal, ErrorBoundary } from 'shared/components';
import { StaffManagementContextProvider } from './context/StaffManagementContext';
import { Helmet } from 'react-helmet';
import { useFormGuard, useUnreadCaseTab, useRouter } from '../../shared/hooks';
import { StaffManagementSearchQuery } from './components/StaffManagementSearchQuery';
import { StaffManagementTable } from './components/StaffManagementTable';
import { SimpleBtn, WhiteModal } from 'shared/components/Elements';
import CreateStaffForm from './components/CreateStaffForm';
import { useAppState } from '../../shared/state';
import { useStaffManagementList } from './hooks/useStaffManagement';
import clsx from 'clsx';
import { useSubscription } from '../Payments/hooks';
import { useAuth } from '../../modules/auth/hooks/useAuth';

function StaffManagementRequest() {
	const { isAdmin, isPermitted } = useAppState();
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('Staff Management');

	const [formOpen, setFormOpen] = useState<boolean>(false);
	const { formValues, guardOpen, setGuardOpen, formValuesSetter } = useFormGuard();
	const [extraPaymentOpen, setExtraPaymentOpen] = useState(false);
	const { staffCountLoading, staffCountResponse, paidMemberCount, fetchStaffCount } =
		useStaffManagementList();
	const { history } = useRouter();
	const { subscription } = useSubscription();

	const { company, userData } = useAuth();
	const isOwner = company?.company?.owner?.id === userData.id;

	const updatedVariables = useRef<any>({});

	updatedVariables.current.subscription = subscription;
	updatedVariables.current.staffCountResponse = staffCountResponse;
	updatedVariables.current.paidMemberCount = paidMemberCount;

	//TODO: move to custom hook all payment related logics
	const modalHandlerOpen = useCallback(() => {
		const { staffCountResponse, subscription, paidMemberCount } = updatedVariables.current || {};
		if (
			(staffCountResponse as any)?.staffs_count < paidMemberCount ||
			(subscription?.subscription_type === 'TRIAL' && subscription?.is_available)
		) {
			setFormOpen((prev) => !prev);
		} else {
			setExtraPaymentOpen(true);
		}
	}, [staffCountResponse, subscription, paidMemberCount]);

	const closeExtraPaymentPopup = () => {
		setExtraPaymentOpen(false);
	};

	const navigateToCheckout = () => history.push('/checkout');

	const confirmToggleHandler = useCallback(() => {
		setGuardOpen((prev: boolean) => !prev);
	}, []);
	const confirmModalHandler = useCallback(() => {
		setGuardOpen(false);
		formValuesSetter([]);
		modalHandlerOpen();
	}, [formOpen]);

	const hasToShowGuard = useCallback(() => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}

		modalHandlerOpen();
	}, [formValues]);

	const onPopupClose = () => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}
		modalHandlerOpen();
	};

	return (
		<div className="relative flex min-h-full max-w-full flex-grow flex-col p-4">
			{(!isHidden || !isOwner || unreadCasesQuantity === 0) && (
				<Helmet>
					<title>KENNEKT | Staff Management</title>
				</Helmet>
			)}
			<div className="mb-5">
				<div className=" absolute top-0 left-0 w-full bg-kred-pink-200">
					{!isAdmin ? (
						<p className="my-1 text-center text-xs text-kred-pink">
							You do not have permission to perform some actions, please check with admin
						</p>
					) : null}
				</div>
				<div className="mt-6 flex w-full flex-wrap items-center justify-between">
					<div className="mb-4 flex flex-1 items-center font-bold">
						<h2 className="text-xl dark:text-white md:text-3xl ">Staff Management</h2>
						{subscription && subscription.order && subscription.paid_staffs_count ? (
							<span className="ml-4">
								<span className="mr-1 text-sm font-light text-kgrey-dark-3 dark:text-white">
									Users count:
								</span>
								<span className="text-base font-semibold text-primary">
									{(staffCountResponse as any)?.staffs_count ?? 0}/{subscription.paid_staffs_count}
								</span>
							</span>
						) : null}
					</div>

					<div className="ml-auto mb-4  flex max-w-full items-center">
						<StaffManagementSearchQuery />
						<SimpleBtn
							disabled={!isPermitted}
							type="button"
							onClick={modalHandlerOpen}
							className={clsx({
								'pointer-events-none opacity-20': staffCountLoading
							})}
						>
							Add staff
						</SimpleBtn>
					</div>
				</div>
			</div>
			<StaffManagementTable fetchStaffCount={fetchStaffCount} />
			<ConfirmModal
				featureHandler={navigateToCheckout}
				isOpen={extraPaymentOpen}
				toggleHandler={closeExtraPaymentPopup}
				title={`You have reached limit to add staffs. Open checkout page?`}
			/>
			<WhiteModal
				handleModalOpen={onPopupClose}
				open={formOpen}
				maxWidth={446}
				noPadding
				scrollable
			>
				<CreateStaffForm
					modalHandler={modalHandlerOpen}
					hasToShowGuard={hasToShowGuard}
					formValuesSetter={formValuesSetter}
					fetchStaffCount={fetchStaffCount}
				/>
			</WhiteModal>
			<ConfirmModal
				featureHandler={confirmModalHandler}
				isOpen={guardOpen}
				toggleHandler={confirmToggleHandler}
				title="You have unsaved changes, Are you sure to cancel?"
			/>
		</div>
	);
}
export function StaffManagement() {
	return (
		<ErrorBoundary>
			<StaffManagementContextProvider>
				<StaffManagementRequest />
			</StaffManagementContextProvider>
		</ErrorBoundary>
	);
}
