import { useAppState } from '../../../shared/state';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { useAxios, useNotistack, useRouter } from '../../../shared/hooks';
import { useCallback, useEffect, useState } from 'react';
import { CompSubscriptionType } from '../../../modules/user/reducers';

export function useSubscription() {
	const { companyId } = useAppState();
	const { companies } = useAuth();
	const { history } = useRouter();
	const [cancelLoading, setCancelLoading] = useState(false);
	const [cancelSubscription, setCancelSubscription] = useState(false);
	const { showNotification } = useNotistack();
	const [hasUserAlreadyPaidInvoice, setUserAlreadyPaidInvoice] = useState(false);

	const [subscription, setSubscription] = useState<CompSubscriptionType | null>(null);

	const { fetchData: cancelSubscriptionRequest } = useAxios({
		url: `/${companyId}/payment/${companyId}/cancel-subscription/`,
		method: 'put',
		cb: {
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			success: () => {
				showNotification({ message: 'Your subscription has been cancelled', variant: 'success' });
				setCancelLoading(false);
				setCancelSubscription(false);
			},
			error: (error, additionalArgs) => {
				showNotification({
					message: error?.response?.data?.message || 'Something went wrong',
					variant: 'error'
				});
				setCancelLoading(false);
			}
		}
	});

	const { fetchData: firstPayRequest } = useAxios({
		url: `/payment/${companyId}/invoices/?page_size=10`,
		cb: {
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			success: (data) => {
				if (data?.results?.length) {
					setUserAlreadyPaidInvoice(true);
				}
			},
			error: (error, additionalArgs) => {}
		}
	});

	//set subscription object
	useEffect(() => {
		firstPayRequest();
		if (companyId && companies.length) {
			const currentCompany = companies.find((com) => com.company.id === companyId);
			const currentSubscription = currentCompany?.company.comp_subscription as CompSubscriptionType;

			if (currentSubscription) {
				setSubscription(currentSubscription);
			}
		}
	}, [companies, companyId]);

	const handleCancelRequest = useCallback(async () => {
		setCancelLoading(true);
		await cancelSubscriptionRequest();
	}, []);

	const navigateToCheckout = useCallback(() => history.push('/checkout'), []);

	return {
		navigateToCheckout,
		subscription,
		handleCancelRequest,
		setCancelSubscription,
		cancelSubscription,
		cancelLoading,
		hasUserAlreadyPaidInvoice
	};
}
