import { Avatar } from '@material-ui/core';

export const getBusinessIcon = (url: string | undefined, name: string | undefined) => {
	if (url) {
		return (
			<Avatar
				className="inline-block"
				alt={name || 'Business icon'}
				src={url}
				style={{ width: '100%', height: '100%' }}
			/>
		);
	} else {
		return (
			<Avatar className="inline-block" style={{ color: '#fff', width: '100%', height: '100%' }}>
				{name ? name[0].toUpperCase() : null}
			</Avatar>
		);
	}
};
